
.photo__item {

  &--panorama {
    margin-top: 15px;
  }
}

.photo__wrapper {
  .container;
}

.photo__list {
  .reset-list;
  width: 280px;
  margin: auto;
}

.photo__panorama-mark {
  .container;
  position: relative;
  z-index: 1;

  &::before {

    .pseudo(140px;36px);
    content: "Панорама дня";
    top: 0;
    right: 20px;

    padding-top: 6px;

    background-color: @pink;
    text-transform: uppercase;
    color: @white;
    .font(12px;24px);
    font-weight: 700;
  }
}

.photo__item {
  margin-bottom: 20px;
}

.photo__link-image {
  display: block;
  overflow: hidden;
  line-height: 0;
}

.photo__image {
  width: 100%;
}

.photo__description {
  padding: 18px;
  padding-bottom: 19px;

  text-align: left;
  background-color: @gray;
}

.photo__item--panorama  .photo__description {
  max-width: 280px;
    margin: auto;
}

.photo__user-name {
  .reset-list();
  .font(14px; 24px;);
  font-weight: 700;
}

.photo__user-last-time {
  .font(12px; 24px;);
  font-weight: 300;
  text-transform: uppercase;
}

.photo__comment {
  .reset-list();
  margin-bottom: 10px;

  .font(14px; 24px;);
}

.photo__wrapper-likes {
  .reset-list;
  display: flex;
  align-items: flex-start;
}

.photo__likes {
  padding-left: 19px;

  .font(12px; 24px;);
  color: @pink;
  text-transform: uppercase;
  vertical-align: bottom;
}

.photo__push-like {
  order: -1;
  .reset-list;

  background: none;
  border: none;
  .font(0;0);
}

.photo__icon-like {
  margin-top: 5px;
}

@media (min-width: @tablet-width) {

  .photo__panorama-mark::before {
    top: 55px;
  }

  .photo__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: auto;
    margin-left: -20px;
    margin-right: -20px;
    margin-bottom: -20px;
  }

  .photo__item {
    display: flex;
    flex-direction: column;

    margin: 10px;
    width: 300px;

    &--panorama {
      margin: 0;
      margin-bottom: 40px;
      width: 100%;
    }
  }

  .photo__link-image--panorama .photo__image {
    transform: translateX(-20px);
    width: 106%;
    max-width: 106%;
  }

  .photo__description {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 155px;
    padding: 0 28px;
    padding-top: 24px;
    padding-bottom: 23px;
    flex-grow: 1;
  }

  .photo__item--panorama  .photo__description {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
    align-content: flex-start;

    max-width: 620px;
    min-height: auto;
  }

  .photo__item--panorama .photo__user-name {
    flex-grow: 2;
    order: -2;
  }

  .photo__item--panorama .photo__wrapper-likes {
    justify-content: flex-end;

    flex-grow: 1;
    order: -1;

    text-align: right;
  }

  .photo__item--panorama  .photo__comment {
    margin-bottom: 0;
  }

  .photo__comment {
    flex-grow: 1;
  }

  .photo__wrapper-likes {
    justify-content: space-between;
  }

  .photo__push-like {
    order: 0;
    margin-right: 4px;
  }

  .photo__likes {
    padding: 0;
    padding-right: 18px;
  }
}

@media (min-width: @desktop-width) {
  .photo__wrapper {
    .container-desk;
  }

  .photo__panorama-mark {
    .container-desk;
  }

  .photo__panorama-mark::before {
    right: 10px;
  }

  .photo__list {
    margin-bottom: -44px;
  }

  .photo__item--panorama  .photo__description {
    max-width: 100%;
  }

  .photo__item:not(.photo__item--panorama) {
    margin-bottom: 44px;
  }

  .photo__link-image--panorama .photo__image {
    transform: none;
    width: 100%;
    max-width: 100%;
  }

}
